@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;300;400&display=swap);
*{margin:0;padding:0;list-style:none;text-decoration:none;box-sizing:border-box;scroll-behavior:smooth;font-family:"Roboto Mono", monospace}

.App{height:100vh;display:flex;justify-content:center;align-items:center;transition:0.7s all ease-in}#quote-box{-webkit-animation:fadein 2s;animation:fadein 2s;background-color:white;width:35rem;padding:2rem;display:flex;flex-direction:column;grid-gap:1.5rem;gap:1.5rem;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}#quote-box #text{font-weight:400;text-align:center}#quote-box #author{font-weight:300;text-align:right}.user-section{display:flex;justify-content:space-between}.user-section .icon-quote{margin-right:0.5rem;height:100%}.user-section #new-quote{padding:0.6rem;font-size:1rem;outline:none;border:none;color:white;cursor:pointer}@-webkit-keyframes fadein{from{opacity:0}to{opacity:1}}@keyframes fadein{from{opacity:0}to{opacity:1}}

